/* App.css */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #000; /* Dark background */
  color: #fff; /* Light text */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.App {
  text-align: center;
}

.hero h1 {
  font-size: 2.5rem;
  margin: 0;
  font-weight: bold;
}

.hero .highlight {
  color: #4DB6AC; /* Accent color for name */
}

.hero .roles {
  font-size: 1.2rem;
  font-weight: 300;
  margin-top: 0.5rem;
}

.icons {
  margin-top: 1rem;
}

.icons a {
  color: #4DB6AC; /* Icon color */
  margin: 0 0.5rem;
  transition: transform 0.3s ease;
}

.icons a:hover {
  transform: scale(1.1);
}

.links {
  margin-top: 2rem;
  font-size: 1rem;
}

.links a {
  color: #4DB6AC; /* Accent color for links */
  margin: 0 1rem;
  text-decoration: none;
  transition: color 0.3s ease;
}

.links a:hover {
  color: #66c2b7;
}
